body {
  margin: 0;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#f5f5f5 ;
  height: 100%;
  width:100%;
  margin:auto;
  font-family: 'Lato', sans-serif;;
  overflow-x: hidden;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}



.container{
  display: flex;
  align-items: center;
  justify-content: center;
 

 
}

.canvas{
  
  position:relative;
  left:100px;
  top:0px;
  background-color: #fefefe;
  margin-top: 80px;
  width: calc(99% - 210px) !important;
  
  height:80%;
  border: 1px solid #311b92;

}

nav{
  position: absolute;
  top: 0px;
  left:0px;
  width:100vw;
  height:70px;
  background-color: #311b92;
  color:#fafafa;
  display:flex;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  font-size:40px;
  padding-left:25px;
  font-weight: 500;

  
}

.rect{
  width:100px;
  height:200px;
  border: 1px solid rgb(68, 2, 2);
}

label{
  background-color: white;
  color:#311b92;
  height:35px;
  
  font-size:16px;
  display:flex;
  align-items: center;
  justify-content: center;
  width:100%;
 
}

#controls{
  position:absolute;
  left:0px;
  top:70px;
  padding: 15px;
  width:150px;
  height:calc(100% - 110px);
  margin-right:40px;
  background-color: white;
  display:flex;
  flex-direction:column;

}

@media print {
  body * {
    visibility: hidden;
  }
  .container #controls {
    visibility: hidden;
  }

  .container canvas{
    visibility: visible;
  }

  .container div{
    visibility: visible;
  } 
 
}

.section{
  margin-top:15px;
  display:flex;
  /* padding-left:10px; */
  flex-direction: column;
}

.section .header{
  font-size: 12px;
  border-bottom: 1px solid purple;
}

.section div{
  margin-top:10px;
}

.section div button{
  color: #311b92;
  background-color: white;
  border: 1px solid #311b92;
  width:100%;
  height: 30px;
  border-radius: 5px !important;
  
}

.section div input{
  margin-left:20px !important;
  width:70px !important;
  
}

.section div select{
  margin-left:20px !important;
  height:25px;
  width: 70px;
}

.section div span{
  /* margin-left:20px !important; */
  width:70px !important;
  text-align: left;
  
  
}



.section div button:hover{
  color: white;
  background-color:#311b92 ;
  border: 1px solid white;
  
}

.note{
  font-size: 11px;
  color:darkgrey;
}



#clear{
  position:absolute;
  top:15px;
  right:190px;
}

#download{
  position:absolute;
  top:15px;
  right:30px;
}

.top_button{
  height: 35px;
  width:130px;
  border: 1px solid #311b92;
  background-color: white;
  color: #311b92;
}

.top_button:hover{
  background-color:#d1c4e9 ;
  color: white;
}

